:local {
  .content {
    background-image: url('https://d292eo2ec5lzlq.cloudfront.net/consumer_bg.jpg');
    height: 100%;
    overflow: auto;
    padding: 20px;
  }

  .field-required-message {
    margin-bottom: 1.4385714285714286rem;
    margin-top: 0;
  }

  .section {
    margin-bottom: 20px;
  }

  .boldylocks {
    font-weight: 700;
  }

  .fake-attachments {
    background-color: '#e0e0e0';
    display: block;
    height: '64px';
  }

  .field-help {
    color: #434a4d;
    font-size: 85%;
    outline: 0;
    padding-top: 5px;
  }

  .sent-on-behalf {
    color: #1c1f21;
    font-weight: 700;
    margin-bottom: 3px;
  }

  .messaging-input {
    background-color: #f4f4f4;
    border: 1px solid #dedfe0;
    border-radius: 0.25em;
    box-shadow: none;
    display: block;
    font-size: 16px;
    line-height: 1.428571rem;
    margin: 0;
    padding: 0.285714rem;
    width: 100%;

    &:focus {
      outline: none;
    }
  }

  .read-only-input {
    border: 0;
  }

  .send-button {
    background-color: #0065a3;
    border: 1px solid #c8cacb;
    border-color: #003656;
    border-radius: 0.25em;
    color: #f4f4f4;
    cursor: pointer;
    display: inline-block;
    font-size: 1rem;
    line-height: 1.428571rem;
    margin-right: 0.714285rem;
    max-width: 100%;
    padding: 0.2857142rem 0.71428571rem;
  }

  .cancel-button {
    background-color: transparent;
    border-color: transparent;
    box-shadow: none;
    color: #0065a3;
    cursor: pointer;
    display: inline-block;
    max-width: 100%;
    text-decoration: none;
  }

  .post-body {
    border: dashed 3px #d1d1d1;
    color: #424242;
    margin-top: 10px;
    padding: 0 10px 10px;
  }
}
